// /components/PostList.tsx
import { useState } from "react";
import { Post } from "../lib/posts";
import PostCard from "./PostCard";
import styles from "../styles/PostList.module.css";

interface PostListProps {
  posts: Post[];
  pageSize?: number; // ÊØèÈ°µÊòæÁ§∫Êï∞ÈáèÔºåÈªòËÆ§ 10
}

export default function PostList({ posts, pageSize = 10 }: PostListProps) {
  const [viewMode, setViewMode] = useState<"card" | "list">("card");
  const [currentPage, setCurrentPage] = useState(1);

  const totalPages = Math.ceil(posts.length / pageSize);
  const startIndex = (currentPage - 1) * pageSize;
  const paginatedPosts = posts.slice(startIndex, startIndex + pageSize);

  return (
    <div>
      {/* ÂàáÊç¢ÂõæÊ†áÊåâÈíÆ */}
      <div className={styles.viewSwitch}>
        <img
          src="/icon/view_gallery.svg"
          alt="Âç°ÁâáËßÜÂõæ"
          className={styles.iconButton}
          onClick={() => setViewMode("card")}
        />
        <img
          src="/icon/view_list.svg"
          alt="ÂàóË°®ËßÜÂõæ"
          className={styles.iconButton}
          onClick={() => setViewMode("list")}
        />
      </div>

      {/* posts ÂàóË°® */}
      <div
        style={{
          display: "grid",
          gridTemplateColumns: viewMode === "card" ? "repeat(3, 1fr)" : "1fr",
          gap: "1rem",
        }}
      >
        {paginatedPosts.map((post) => (
          <PostCard key={post.slug} post={post} view={viewMode} />
        ))}
      </div>

      {/* ÁøªÈ°µÊåâÈíÆ */}
      {totalPages > 1 && (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginTop: "1.5rem",
            gap: "0.5rem",
          }}
        >
          <button
            onClick={() => setCurrentPage((p) => Math.max(p - 1, 1))}
            disabled={currentPage === 1}
            style={{
              padding: "0.5rem 1rem",
              borderRadius: "5px",
              border: "1px solid #ccc",
              cursor: currentPage === 1 ? "not-allowed" : "pointer",
            }}
          >
            Prev
          </button>

          <span style={{ padding: "0.5rem 1rem" }}>
            {currentPage} / {totalPages}
          </span>

          <button
            onClick={() => setCurrentPage((p) => Math.min(p + 1, totalPages))}
            disabled={currentPage === totalPages}
            style={{
              padding: "0.5rem 1rem",
              borderRadius: "5px",
              border: "1px solid #ccc",
              cursor: currentPage === totalPages ? "not-allowed" : "pointer",
            }}
          >
            Next
          </button>
        </div>
      )}
    </div>
  );
}
